<template>
  <div>
    <div class="container">
      <van-notice-bar
        v-if="permit.notice_bar"
        mode="closeable"
        background="#22b37c"
        color="#22b37c"
        text="这个小程序只是第一个雏形，欢迎向我提更多的需求，致力于帮你们做好这件事。"
      />
      <img class='shi' src="https://7465-test-ad5164-1257858866.tcb.qcloud.la/img/shi.png?sign=73ef7ae9524d21008d684c3fdbc5c7b3&t=1540791397" alt="">
      <div class='content'>
          <input type="text" v-model='value' placeholder="请输入小区名称" placeholder-style="color: #22b37c;" class='input'>
          <view class="button"  @click='serach'>搜一搜</view>
      </div>
    </div>
    <div class="history">
      <p><span>历史搜索</span> <span style="color: #22b37c" @click="clearStorage">清除搜索</span></p>
      <div style="margin-top: 10px;">
        <span class="his_item" v-for="(item, index) in historyList" :key="index"  @click='itmeClick(item)'>
          {{item}} 
        </span>
      </div>
    </div>
    <img v-if="permit.add_enabled" @click='onAdd' class='jia' src="https://7465-test-ad5164-1257858866.tcb.qcloud.la/img/jia.png?sign=fb30e796a732e90aa83f935b9133a330&t=1540791495" alt="">
    <div class="show" v-if="show">
      <div class='item'>
        <view class="label"><span style="color: #f00">*</span> 输入密码：</view>
        <input type="text" v-model='paw' placeholder="请输入密码" class='input'>
      </div>
      <button class="button2" @click="cleckEnable">保存</button>
    </div>
    <van-notify id="van-notify" />  
  </div>
</template>

<script>
import Notify from "@/../static/vant/notify/notify";
import Dialog from '@/../static/vant/dialog/dialog';

export default {
  data() {
    return {
      value: "",
      historyList: [],
      permit: {},
      show: false,
    };
  },

  methods: {
    cleckEnable() {
       this.show = false 
      if(this.paw == '5210') {
        wx.setStorageSync("pwd_enabled", 1);
        const url = "../add/main";
        wx.navigateTo({ url });
      }else{
        Notify("密码错误");
      }
    },
    onAdd() {
      var pwd_enabled = wx.getStorageSync("pwd_enabled");
      if(!pwd_enabled) {
        this.show = true 
        return
      }
      const url = "../add/main";
      wx.navigateTo({ url });
    },
    itmeClick (item) {
      const url = `../list/main?value=${item}`;
      wx.navigateTo({ url });
    },
    clearStorage () {
      this.historyList = []
      wx.clearStorageSync()
    },
    serach: function() {
      if (!this.value) return Notify("请输入小区名称");
      const url = `../list/main?value=${this.value}`;
      var history = wx.getStorageSync("history");
      if (history) {
        const a = history.split(",").indexOf("" + this.value) == -1;
        if (a) wx.setStorageSync("history", history + "," + this.value);
      }else {
        wx.setStorageSync("history", this.value);
      }
      wx.navigateTo({ url });
      this.value = "";
    }
  },
  async onShow() {
    const db = wx.cloud.database();
    const { data } = await db.collection("permit").get();
    this.permit = data[0];
    var his = wx.getStorageSync("history").split(",");
    this.historyList = his;
  }
};
</script>

<style scoped>
.container {
  min-height: 200px;
  background: #22b37c;
  text-align: center;
}

.shi {
  margin-top: 20px;
  width: 100px;
  height: 60px;
}
.content {
  display: flex;
  align-items: center;
  width: 280px;
  text-align: center;
  margin: 0 auto;
  margin-top: 20px;
}
.input {
  flex: 1;
  background: #fff;
  font-size: 14px;
  height: 40px;
  line-height: 40px;
  border-top-left-radius: 5%;
  border-bottom-left-radius: 5%;
}
.button {
  border-radius: 5%;
  font-size: 13px;
  height: 40px;
  line-height: 40px;
  width: 80px;
  color: #fff;
  background: #028668;
  border: 0;
  border-top-right-radius: 5%;
  border-bottom-right-radius: 5%;
}
.jia {
  position: fixed;
  right: 10px;
  bottom: 20%;
  width: 30px;
  height: 30px;
}
.history {
  padding: 15px;
}
.history p {
  font-size: 16px;
  color: #333;
  display: flex;
  justify-content: space-between;
}
.his_item {
  display: inline-block;
  width: 60px;
  color: #888;
  font-size: 13px;
  white-space:nowrap;
  text-overflow:ellipsis;
  -o-text-overflow:ellipsis; 
  overflow:hidden;
  text-align: center;
}
.show {
  padding: 15px;
}
.item {
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  height: 38px;
  margin-bottom: 15px;
}
.item .label {
  width: 120px;
  font-size: 16px;
}
.item .input {
  flex: 1;
  font-size: 16px;
  border-bottom: 1px solid #ddd;
}
.button2 {
  background: #22b37c;
  color: #fff;
}
</style>



